export class CaseModel {
    Id: number = 0;
    CaseNumber: string = "";
    CaseDate: Date = new Date();
    CitizenId: number = 0;
    Citizen: string = "";
    CitizenDateOfBirth: string = "";
    CitizenAge:  number = 0;
    CitizenCivilStatus: string = "";
    CitizenEducationalAttainment: string = "";
    CitizenOccupation: string = "";
    CitizenReligion: string = "";
    CitizenAddress: string = "";
    CitizenCardId: number = 0;
    CitizenCardNumber: number = 0;
    ServiceId: number = 0;
    Problem: string = "";
    Background: string = "";
    Recommendation: string = "";
    PreparedById: number = 0;
    CheckedById: number = 0;
    StatusId: number = 0;
    IsLocked: boolean = false;
}