import { Component, OnInit, Inject, ViewContainerRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CardService } from '../../card.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-card-print-card-dialog',
  templateUrl: './card-print-card-dialog.component.html',
  styleUrls: ['./card-print-card-dialog.component.css']
})
export class CardPrintCardDialogComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public cardPrintCardDialogRef: MatDialogRef<CardPrintCardDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public cardData: any,
    private toastr: ToastrService
  ) { }

  public printCardSubscription: any;
  public pdfUrl: string;

  public printCard(): void {
    this.cardService.printCard(this.cardData.objId);
    this.printCardSubscription = this.cardService.printCardObservable.subscribe(
      data => {
        var binaryData = [];
        binaryData.push(data);
        this.pdfUrl = URL.createObjectURL(new Blob(binaryData, { type: "application/pdf" }));

        let printPDF: Element = document.getElementById("printPDF");
        printPDF.setAttribute("src", this.pdfUrl);

        if (this.printCardSubscription != null) this.printCardSubscription.unsubscribe();
      }
    );
  }

  public btnClosePrintDialog(): void {
    this.cardPrintCardDialogRef.close();
    if (this.printCardSubscription != null) this.printCardSubscription.unsubscribe();
  }

  public btnPrintCard(): void {
    window.frames["printPDF"].focus();
    window.frames["printPDF"].print();
  }

  ngOnInit() {
    this.printCard();
  }

  ngOnDestroy() {
    if (this.printCardSubscription != null) this.printCardSubscription.unsubscribe();
  }

}
