import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Subject, Observable } from 'rxjs';
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-upload-photo-dialog',
  templateUrl: './citizen-upload-photo-dialog.component.html',
  styleUrls: ['./citizen-upload-photo-dialog.component.css']
})
export class CitizenUploadPhotoDialogComponent implements OnInit {
  constructor(
    private citizenService: CitizenService,
    public uploadPhotoDialogRef: MatDialogRef<CitizenUploadPhotoDialogComponent>,
    private toastr: ToastrService,
    @Inject(MAT_DIALOG_DATA) public uploadPhotoData: any,
  ) { }

  public uploadPhotoCitizenSubscription: any;

  public btnUploadPhoto(): void {
    let btnUploadPhoto: Element = document.getElementById("btnUploadPhoto");
    let btnCloseUploadPhotoDialog: Element = document.getElementById("btnCloseUploadPhotoDialog");

    btnUploadPhoto.setAttribute("disabled", "disabled");
    btnCloseUploadPhotoDialog.setAttribute("disabled", "disabled");

    let inputFileImage = document.getElementById("inputFileUploadCitizensPhoto") as HTMLInputElement;

    if (inputFileImage.files.length > 0) {
      this.citizenService.uploadPhotoCitizen(inputFileImage.files[0], this.uploadPhotoData.objCitizensName, this.uploadPhotoData.objCitizensModel.Id);
      this.uploadPhotoCitizenSubscription = this.citizenService.uploadCitizenPhotoObservable.subscribe(
        data => {
          if (data[0] == "success") {
            this.toastr.success("Photo was successfully uploaded.", "Success");
            this.uploadPhotoDialogRef.close(data[1]);
          } else if (data[0] == "failed") {
            this.toastr.error(data[1], "Error");
            btnUploadPhoto.removeAttribute("disabled");
            btnCloseUploadPhotoDialog.removeAttribute("disabled");
          }

          if (this.uploadPhotoCitizenSubscription != null) this.uploadPhotoCitizenSubscription.unsubscribe();
        }
      );
    } else {
      this.toastr.error("No image file selected.", "Error");
      btnUploadPhoto.removeAttribute("disabled");
      btnCloseUploadPhotoDialog.removeAttribute("disabled");
    }
  }

  public btnCloseUploadPhotoDialog(): void {
    this.uploadPhotoDialogRef.close(null);
  }

  ngOnInit() {
  }
}
