import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CardModel } from '../card.model'
import { CardService } from '../card.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';
import { CardPrintCardDialogComponent } from './card-print-card-dialog/card-print-card-dialog.component';

@Component({
  selector: 'app-card-detail-dialog',
  templateUrl: './card-detail-dialog.component.html',
  styleUrls: ['./card-detail-dialog.component.css']
})
export class CardDetailDialogComponent implements OnInit {

  constructor(
    private cardService: CardService,
    public cardDetailDialogRef: MatDialogRef<CardDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public cardData: any,
    private toastr: ToastrService,
    public cardPrintCardDialog: MatDialog,
  ) { }

  public cardNumberShow: boolean = this.cardData.objIsCardNumberShow;
  public cardPrintDisabled: boolean = this.cardData.objIsCardPrintDisabled;
  public dialogTitle: string = "Card Detail";
  public cardId: number = 0;

  public cardModel: CardModel = {
    Id: this.cardData.objCardModel.Id,
    CitizenId: this.cardData.objCardModel.CitizenId,
    Citizen: this.cardData.objCardModel.Citizen,
    CardNumber: this.cardData.objCardModel.CardNumber,
    TotalBalance: this.cardData.objCardModel.TotalBalance,
    StatusId: this.cardData.objCardModel.StatusId
  }

  public listCardDropdownStatusSubscription: any;
  public listCardDropdownStatusObservableArray: ObservableArray = new ObservableArray();

  public saveCardSubscription: any;

  public listDropdownStatus(): void {
    this.cardService.listDropdownStatus();
    this.listCardDropdownStatusSubscription = this.cardService.listDropdownCardStatusObservable.subscribe(
      data => {
        this.listCardDropdownStatusObservableArray = data;
        if (this.listCardDropdownStatusSubscription != null) this.listCardDropdownStatusSubscription.unsubscribe();
      }
    );
  }

  public btnSaveCard(): void {
    let btnSaveCard: Element = document.getElementById("btnSaveCard");
    let btnCloseCardDialog: Element = document.getElementById("btnCloseCardDialog");

    btnSaveCard.setAttribute("disabled", "disabled");
    btnCloseCardDialog.setAttribute("disabled", "disabled");

    this.cardService.saveCard(this.cardModel);
    this.saveCardSubscription = this.cardService.saveCardObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Card was successfully saved.", "Success");
          this.cardPrintDisabled = false;
          this.cardId = parseInt(data[1]);
          // this.cardDetailDialogRef.close(200);
          btnSaveCard.removeAttribute("disabled");
          btnCloseCardDialog.removeAttribute("disabled");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          this.cardPrintDisabled = true;
          btnSaveCard.removeAttribute("disabled");
          btnCloseCardDialog.removeAttribute("disabled");
        }

        if (this.saveCardSubscription != null) this.saveCardSubscription.unsubscribe();
      }
    );
  }

  public btnCloseCardDialog(): void {
    this.cardDetailDialogRef.close(200);

    if (this.listCardDropdownStatusSubscription != null) this.listCardDropdownStatusSubscription.unsubscribe();
    if (this.saveCardSubscription != null) this.saveCardSubscription.unsubscribe();
  }

  public btnPrintCard(): void {
    let printCardId = this.cardModel.Id;
    if (this.cardModel.Id == 0) {
      printCardId = this.cardId;
    }

    this.cardPrintCardDialog.open(CardPrintCardDialogComponent, {
      width: '1000px',
      data: { objId: printCardId },
      disableClose: true
    });
  }

  ngOnInit() {
    this.dialogTitle = this.cardData.objDialogTitle;
    this.listDropdownStatus();
  }

  ngOnDestroy() {
    if (this.listCardDropdownStatusSubscription != null) this.listCardDropdownStatusSubscription.unsubscribe();
    if (this.saveCardSubscription != null) this.saveCardSubscription.unsubscribe();
  }

}
