import { Injectable } from '@angular/core';
import { AppSettings } from './../../app-settings';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { ObservableArray } from 'wijmo/wijmo';
import { Subject } from 'rxjs';
import {
  CaseModel
} from './ayuda.model'

@Injectable({
  providedIn: 'root'
})
export class AyudaService {

  constructor(
    private appSettings: AppSettings,
    private httpClient: HttpClient
  ) { }

  public defaultAPIURLHost: string = this.appSettings.APIURLHost;
  public options: any = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + localStorage.getItem('access_token')
    })
  };

  public listServiceDepartmentSubject = new Subject<ObservableArray>();
  public listServiceDepartmentObservable = this.listServiceDepartmentSubject.asObservable();

  public listCaseSubject = new Subject<ObservableArray>();
  public listCaseObservable = this.listCaseSubject.asObservable();
  public detailCaseSubject = new Subject<CaseModel>();
  public detailCaseObservable = this.detailCaseSubject.asObservable();
  public listCitizenSubject = new Subject<ObservableArray>();
  public listCitizenObservable = this.listCitizenSubject.asObservable();
  public listCitizensCardSubject = new Subject<ObservableArray>();
  public listCitizensCardObservable = this.listCitizensCardSubject.asObservable();
  public listServiceSubject = new Subject<ObservableArray>();
  public listServiceObservable = this.listServiceSubject.asObservable();
  public listServiceGroupTemplateSubject = new Subject<ObservableArray>();
  public listServiceGroupTemplateObservable = this.listServiceGroupTemplateSubject.asObservable();
  public listUserSubject = new Subject<ObservableArray>();
  public listUserObservable = this.listUserSubject.asObservable();
  public listStatusSubject = new Subject<ObservableArray>();
  public listStatusObservable = this.listStatusSubject.asObservable();
  public addCaseSubject = new Subject<string[]>();
  public addCaseObservable = this.addCaseSubject.asObservable();
  public saveCaseSubject = new Subject<string[]>();
  public saveCaseObservable = this.saveCaseSubject.asObservable();
  public lockCaseSubject = new Subject<string[]>();
  public lockCaseObservable = this.lockCaseSubject.asObservable();
  public unlockCaseSubject = new Subject<string[]>();
  public unlockCaseObservable = this.unlockCaseSubject.asObservable();
  public printCaseSubject = new Subject<Blob>();
  public printCaseObservable = this.printCaseSubject.asObservable();
  public deleteCaseSubject = new Subject<string[]>();
  public deleteCaseObservable = this.deleteCaseSubject.asObservable();

  public listCardSubject = new Subject<ObservableArray>();
  public listCardObservable = this.listCardSubject.asObservable();
  public searchCardListSource = new Subject<ObservableArray>();
  public searchCardListObservable = this.searchCardListSource.asObservable();

  public listServiceDepartment(): void {
    let listServiceDepartmentObservableArray = new ObservableArray();
    this.listServiceDepartmentSubject.next(listServiceDepartmentObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/serviceDepartment/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listServiceDepartmentObservableArray.push({
              Id: results[i].Id,
              ServiceDepartment: results[i].ServiceDepartment
            });
          }
        }

        this.listServiceDepartmentSubject.next(listServiceDepartmentObservableArray);
      }
    );
  }

  public listCase(serviceDepartmentId: number, startDate: string, endDate: string): void {
    let listCaseObservableArray = new ObservableArray();
    this.listCaseSubject.next(listCaseObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/list/" + serviceDepartmentId + "/" + startDate + "/" + endDate, this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCaseObservableArray.push({
              Id: results[i].Id,
              CaseNumber: results[i].CaseNumber,
              CaseDate: results[i].CaseDate,
              CitizenId: results[i].CitizenId,
              Citizen: results[i].Citizen,
              CitizenDateOfBirth: results[i].CitizenDateOfBirth,
              CitizenAge: results[i].CitizenAge,
              CitizenCivilStatus: results[i].CitizenCivilStatus,
              CitizenEducationalAttainment: results[i].CitizenEducationalAttainment,
              CitizenOccupation: results[i].CitizenOccupation,
              CitizenReligion: results[i].CitizenReligion,
              CitizenAddress: results[i].CitizenAddress,
              CitizenCardId: results[i].CitizenCardId,
              CitizenCardNumber: results[i].CitizenCardNumber,
              ServiceId: results[i].ServiceId,
              Service: results[i].Service,
              ServiceGroup: results[i].ServiceGroup,
              Problem: results[i].Problem,
              Background: results[i].Background,
              Recommendation: results[i].Recommendation,
              PreparedById: results[i].PreparedById,
              PreparedBy: results[i].PreparedBy,
              CheckedById: results[i].CheckedById,
              CheckedBy: results[i].CheckedBy,
              StatusId: results[i].StatusId,
              Status: results[i].Status,
              IsLocked: results[i].IsLocked,
              CreatedByUserId: results[i].CreatedByUserId,
              CreatedByUser: results[i].Fullname,
              CreatedDateTime: results[i].CreatedDateTime,
              UpdatedByUserId: results[i].UpdatedByUserId,
              UpdatedByUser: results[i].Fullname,
              UpdatedDateTime: results[i].UpdatedDateTime
            });
          }
        }

        this.listCaseSubject.next(listCaseObservableArray);
      }
    );
  }

  public detailCase(id: number): void {
    let citizenModel: CaseModel;

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/detail/" + id, this.options).subscribe(
      response => {
        let results = response;

        if (results != null) {
          citizenModel = {
            Id: results["Id"],
            CaseNumber: results["CaseNumber"],
            CaseDate: results["CaseDate"],
            CitizenId: results["CitizenId"],
            Citizen: results["Citizen"],
            CitizenDateOfBirth: results["CitizenDateOfBirth"],
            CitizenAge: results["CitizenAge"],
            CitizenCivilStatus: results["CitizenCivilStatus"],
            CitizenEducationalAttainment: results["CitizenEducationalAttainment"],
            CitizenOccupation: results["CitizenOccupation"],
            CitizenReligion: results["CitizenReligion"],
            CitizenAddress: results["CitizenAddress"],
            CitizenCardId: results["CitizenCardId"],
            CitizenCardNumber: results["CitizenCardNumber"],
            ServiceId: results["ServiceId"],
            Problem: results["Problem"],
            Background: results["Background"],
            Recommendation: results["Recommendation"],
            PreparedById: results["PreparedById"],
            CheckedById: results["CheckedById"],
            StatusId: results["StatusId"],
            IsLocked: results["IsLocked"]
          }
        }

        this.detailCaseSubject.next(citizenModel);
      }
    );
  }

  public listCitizen(): void {
    let listCitizenObservableArray = new ObservableArray();
    this.listCitizenSubject.next(listCitizenObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/citizen/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCitizenObservableArray.push({
              Id: results[i].Id,
              Citizen: results[i].Surname + ", " + results[i].Firstname + " " + results[i].Middlename + " " + results[i].Extensionname
            });
          }
        }

        this.listCitizenSubject.next(listCitizenObservableArray);
      }
    );
  }

  public listCitizensCard(citizenId: number): void {
    let listCitizensCardObservableArray = new ObservableArray();
    this.listCitizensCardSubject.next(listCitizensCardObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/citizensCard/dropdown/list/" + citizenId, this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCitizensCardObservableArray.push({
              Id: results[i].Id,
              CardNumber: results[i].CardNumber
            });
          }
        }

        this.listCitizensCardSubject.next(listCitizensCardObservableArray);
      }
    );
  }

  public listService(): void {
    let listServiceObservableArray = new ObservableArray();
    this.listServiceSubject.next(listServiceObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/service/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listServiceObservableArray.push({
              Id: results[i].Id,
              Service: results[i].Service,
              ServiceGroupId: results[i].ServiceGroupId
            });
          }
        }

        this.listServiceSubject.next(listServiceObservableArray);
      }
    );
  }

  public listServiceGroupTemplate(serviceGroupId: number): void {
    let listServiceGroupTemplateObservableArray = new ObservableArray();
    this.listServiceGroupTemplateSubject.next(listServiceGroupTemplateObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/service/group/template/list/" + serviceGroupId, this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listServiceGroupTemplateObservableArray.push({
              Id: results[i].Id,
              ServiceGroupId: results[i].ServiceGroupId,
              ServiceGroup: results[i].ServiceGroup,
              TemplateName: results[i].TemplateName,
              Problem: results[i].Problem,
              Background: results[i].Background,
              Recommendation: results[i].Recommendation
            });
          }
        }

        this.listServiceGroupTemplateSubject.next(listServiceGroupTemplateObservableArray);
      }
    );
  }

  public listUser(): void {
    let listUserObservableArray = new ObservableArray();
    this.listUserSubject.next(listUserObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/user/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listUserObservableArray.push({
              Id: results[i].Id,
              Fullname: results[i].Fullname
            });
          }
        }

        this.listUserSubject.next(listUserObservableArray);
      }
    );
  }

  public listStatus(): void {
    let listStatusObservableArray = new ObservableArray();
    this.listStatusSubject.next(listStatusObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/status/dropdown/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listStatusObservableArray.push({
              Id: results[i].Id,
              Status: results[i].Status
            });
          }
        }

        this.listStatusSubject.next(listStatusObservableArray);
      }
    );
  }

  public addCase(citizensCardId: number): void {
    this.httpClient.post(this.defaultAPIURLHost + "/api/trn/case/add/" + citizensCardId, JSON.stringify(""), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.addCaseSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.addCaseSubject.next(errorResults);
      }
    );
  }

  public saveCase(objCaseModel: CaseModel): void {
    this.httpClient.put(this.defaultAPIURLHost + "/api/trn/case/save/" + objCaseModel.Id, JSON.stringify(objCaseModel), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.saveCaseSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.saveCaseSubject.next(errorResults);
      }
    );
  }

  public lockCase(objCaseModel: CaseModel): void {
    this.httpClient.put(this.defaultAPIURLHost + "/api/trn/case/lock/" + objCaseModel.Id, JSON.stringify(objCaseModel), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.lockCaseSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.lockCaseSubject.next(errorResults);
      }
    );
  }

  public unlockCase(objCaseModel: CaseModel): void {
    this.httpClient.put(this.defaultAPIURLHost + "/api/trn/case/unlock/" + objCaseModel.Id, JSON.stringify(""), this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", response.toString()];
        this.unlockCaseSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.unlockCaseSubject.next(errorResults);
      }
    );
  }

  public printCase(id: number, documentType: string): void {
    let printCaseOptions: any = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + localStorage.getItem('access_token')
      }),
      responseType: "blob"
    };

    if (documentType == "Case Summary Report") {
      this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/print/" + id, printCaseOptions).subscribe(
        response => {
          let results = new Blob([response], { type: 'application/pdf' });
          this.printCaseSubject.next(results);
        }
      );
    }

    if (documentType == "Certificate of Eligibility") {
      this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/print/certificateOfEligibility/" + id, printCaseOptions).subscribe(
        response => {
          let results = new Blob([response], { type: 'application/pdf' });
          this.printCaseSubject.next(results);
        }
      );
    }

    if (documentType == "Disbursement Voucher") {
      this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/print/disbursementVoucher/" + id, printCaseOptions).subscribe(
        response => {
          let results = new Blob([response], { type: 'application/pdf' });
          this.printCaseSubject.next(results);
        }
      );
    }
    
    if (documentType == "Obligation Request") {
      this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/print/obligationRequest/" + id, printCaseOptions).subscribe(
        response => {
          let results = new Blob([response], { type: 'application/pdf' });
          this.printCaseSubject.next(results);
        }
      );
    }
    
    if (documentType == "Journal Entry Voucher") {
      this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/print/journalEntryVoucher/" + id, printCaseOptions).subscribe(
        response => {
          let results = new Blob([response], { type: 'application/pdf' });
          this.printCaseSubject.next(results);
        }
      );
    }
  }

  public deleteCase(id: number): void {
    this.httpClient.delete(this.defaultAPIURLHost + "/api/trn/case/delete/" + id, this.options).subscribe(
      response => {
        let responseResults: string[] = ["success", ""];
        this.deleteCaseSubject.next(responseResults);
      },
      error => {
        let errorResults: string[] = ["failed", error["error"]];
        this.deleteCaseSubject.next(errorResults);
      }
    );
  }

  public listCard(): void {
    let listCardObservableArray = new ObservableArray();
    this.listCardSubject.next(listCardObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/citizensCard/list", this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            listCardObservableArray.push({
              Id: results[i].Id,
              CitizenId: results[i].CitizenId,
              Citizen: results[i].Citizen,
              CardNumber: results[i].CardNumber,
              CitizensDateOfBirth: results[i].CitizensDateOfBirth,
              TotalBalance: results[i].TotalBalance,
              StatusId: results[i].StatusId,
              Status: results[i].Status
            });
          }
        }

        this.listCardSubject.next(listCardObservableArray);
      }
    );
  }

  public searchCard(keyWord: string): void {
    let searchCardObservableArray = new ObservableArray();
    this.searchCardListSource.next(searchCardObservableArray);

    this.httpClient.get(this.defaultAPIURLHost + "/api/trn/case/citizensCard/search/list?keyWord=" + keyWord, this.options).subscribe(
      response => {
        let results = response;

        if (results["length"] > 0) {
          for (let i = 0; i <= results["length"] - 1; i++) {
            searchCardObservableArray.push({
              Id: results[i].Id,
              CitizenId: results[i].CitizenId,
              Citizen: results[i].Citizen,
              CardNumber: results[i].CardNumber,
              CitizensDateOfBirth: results[i].CitizensDateOfBirth,
              TotalBalance: results[i].TotalBalance,
              StatusId: results[i].StatusId,
              Status: results[i].Status
            });
          }
        }

        this.searchCardListSource.next(searchCardObservableArray);
      }
    );
  }
}