import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ServiceGroupModel } from '../services.model'
import { ServicesService } from '../services.service';

import { ToastrService } from 'ngx-toastr';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { ServiceGroupTemplateModel } from '../services.model'
import { ServiceGroupTemplateDetailDialogComponent } from './service-group-template-detail-dialog/service-group-template-detail-dialog.component'
import { ServiceGroupTemplateDeleteDialogComponent } from './service-group-template-delete-dialog/service-group-template-delete-dialog.component'

@Component({
  selector: 'app-service-group-detail-dialog',
  templateUrl: './service-group-detail-dialog.component.html',
  styleUrls: ['./service-group-detail-dialog.component.css']
})
export class ServiceGroupDetailDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceGroupDetailDialogRef: MatDialogRef<ServiceGroupDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceGroupData: any,
    private toastr: ToastrService,
    public serviceGroupTemplateDetailDialog: MatDialog,
    public serviceGroupTemplateDeleteDialog: MatDialog,
  ) { }

  public dialogTitle: string = "Service Group Detail";

  public serviceGroupModel: ServiceGroupModel = {
    Id: this.serviceGroupData.objServiceGroupModel.Id,
    ServiceGroup: this.serviceGroupData.objServiceGroupModel.ServiceGroup,
    Description: this.serviceGroupData.objServiceGroupModel.Description,
    ServiceDepartmentId: this.serviceGroupData.objServiceGroupModel.ServiceDepartmentId
  }

  public listDropdownServiceDepartmentSubscription: any;
  public listDropdownServiceDepartmentObservableArray: ObservableArray = new ObservableArray();

  public saveServiceGroupSubscription: any;

  public listServiceGroupTemplateSubscription: any;
  public listServiceGroupTemplateObservableArray: ObservableArray = new ObservableArray();
  public listServiceGroupTemplateCollectionView: CollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
  public listServiceGroupTemplatePageIndex: number = 10;

  public serviceGroupTemplateModel: ServiceGroupTemplateModel = new ServiceGroupTemplateModel();

  public listDropdownServiceDepartment(): void {
    this.servicesService.listDropdownServiceDepartment();
    this.listDropdownServiceDepartmentSubscription = this.servicesService.listDropdownServiceDepartmentObservable.subscribe(
      data => {
        this.listDropdownServiceDepartmentObservableArray = data;
        if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();

        this.listServiceGroupTemplateData(this.serviceGroupModel.Id);
      }
    );
  }

  public btnSaveServiceGroup(): void {
    let btnSaveServiceGroup: Element = document.getElementById("btnSaveServiceGroup");
    let btnCloseServiceGroupDialog: Element = document.getElementById("btnCloseServiceGroupDialog");

    btnSaveServiceGroup.setAttribute("disabled", "disabled");
    btnCloseServiceGroupDialog.setAttribute("disabled", "disabled");

    this.servicesService.saveServiceGroup(this.serviceGroupModel);
    this.saveServiceGroupSubscription = this.servicesService.saveServiceGroupObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Service group was successfully saved.", "Success");
          this.serviceGroupDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveServiceGroup.removeAttribute("disabled");
          btnCloseServiceGroupDialog.removeAttribute("disabled");
        }

        if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
      }
    );
  }

  public listServiceGroupTemplateData(serviceGroupId: number): void {
    this.listServiceGroupTemplateObservableArray = new ObservableArray();
    this.listServiceGroupTemplateCollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
    this.listServiceGroupTemplateCollectionView.pageSize = 15;
    this.listServiceGroupTemplateCollectionView.trackChanges = true;
    this.listServiceGroupTemplateCollectionView.refresh();

    this.servicesService.listServiceGroupTemplate(serviceGroupId);
    this.listServiceGroupTemplateSubscription = this.servicesService.listServiceGroupTemplateObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listServiceGroupTemplateObservableArray = data;
          this.listServiceGroupTemplateCollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
          this.listServiceGroupTemplateCollectionView.pageSize = this.listServiceGroupTemplatePageIndex;
          this.listServiceGroupTemplateCollectionView.trackChanges = true;
          this.listServiceGroupTemplateCollectionView.refresh();
        }

        if (this.listServiceGroupTemplateSubscription != null) this.listServiceGroupTemplateSubscription.unsubscribe();
      }
    );
  }

  public btnAddServiceGroupTemplate(): void {
    this.serviceGroupTemplateModel = new ServiceGroupTemplateModel();
    this.serviceGroupTemplateModel.ServiceGroupId = this.serviceGroupModel.Id;

    const serviceGroupTemplateDetailDialogRef = this.serviceGroupTemplateDetailDialog.open(ServiceGroupTemplateDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Add Template",
        objServiceGroupTemplateModel: this.serviceGroupTemplateModel
      },
      disableClose: true
    });

    serviceGroupTemplateDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupTemplateData(this.serviceGroupModel.Id);
      }
    });
  }

  public btnEditServiceGroupTemplate(): void {
    let currentCitizensChild = this.listServiceGroupTemplateCollectionView.currentItem;
    this.serviceGroupTemplateModel.Id = currentCitizensChild.Id;
    this.serviceGroupTemplateModel.ServiceGroupId = currentCitizensChild.ServiceGroupId;
    this.serviceGroupTemplateModel.TemplateName = currentCitizensChild.TemplateName;
    this.serviceGroupTemplateModel.Problem = currentCitizensChild.Problem;
    this.serviceGroupTemplateModel.Background = currentCitizensChild.Background;
    this.serviceGroupTemplateModel.Recommendation = currentCitizensChild.Recommendation;

    const serviceGroupTemplateDetailDialogRef = this.serviceGroupTemplateDetailDialog.open(ServiceGroupTemplateDetailDialogComponent, {
      width: '500px',
      data: {
        objDialogTitle: "Edit Template",
        objServiceGroupTemplateModel: this.serviceGroupTemplateModel
      },
      disableClose: true
    });

    serviceGroupTemplateDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupTemplateData(this.serviceGroupModel.Id);
      }
    });
  }

  public btnDeleteServiceGroupTemplate(): void {
    let currentCitizensChild = this.listServiceGroupTemplateCollectionView.currentItem;
    this.serviceGroupTemplateModel.Id = currentCitizensChild.Id;

    const serviceGroupTemplateDeleteDialogRef = this.serviceGroupTemplateDeleteDialog.open(ServiceGroupTemplateDeleteDialogComponent, {
      width: '300px',
      data: { objServiceGroupTemplateModel: this.serviceGroupTemplateModel },
      disableClose: true
    });

    serviceGroupTemplateDeleteDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listServiceGroupTemplateData(this.serviceGroupModel.Id);
      }
    });
  }

  public btnCloseServiceGroupDialog(): void {
    this.serviceGroupDetailDialogRef.close(200);

    if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
    if (this.listServiceGroupTemplateSubscription != null) this.listServiceGroupTemplateSubscription.unsubscribe();
    if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
  }

  ngOnInit() {
    this.listDropdownServiceDepartment();
    this.dialogTitle = this.serviceGroupData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.listDropdownServiceDepartmentSubscription != null) this.listDropdownServiceDepartmentSubscription.unsubscribe();
    if (this.listServiceGroupTemplateSubscription != null) this.listServiceGroupTemplateSubscription.unsubscribe();
    if (this.saveServiceGroupSubscription != null) this.saveServiceGroupSubscription.unsubscribe();
  }

}
