import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CaseModel } from '../ayuda.model';

import { AyudaService } from '../ayuda.service';

import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-case-delete-dialog',
  templateUrl: './case-delete-dialog.component.html',
  styleUrls: ['./case-delete-dialog.component.css']
})
export class CaseDeleteDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseDeleteDialogRef: MatDialogRef<CaseDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public caseData: any,
    private toastr: ToastrService
  ) { }

  public deleteCaseSubscription: any;

  public btnDeleteCase() {
    let btnDeleteCase: Element = document.getElementById("btnDeleteCase");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteCase.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.ayudaService.deleteCase(this.caseData.objCaseModel.Id);
    this.deleteCaseSubscription = this.ayudaService.deleteCaseObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Case was successfully deleted.", "Success");
          this.caseDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteCase.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteCaseSubscription != null) this.deleteCaseSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.caseDeleteDialogRef.close();

    if (this.deleteCaseSubscription != null) this.deleteCaseSubscription.unsubscribe();
  }

  ngOnInit() {

  }

  ngOnDestroy() {
    if (this.deleteCaseSubscription != null) this.deleteCaseSubscription.unsubscribe();
  }

}
