import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AyudaService } from '../../ayuda.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-case-search-card-dialog',
  templateUrl: './case-search-card-dialog.component.html',
  styleUrls: ['./case-search-card-dialog.component.css']
})
export class CaseSearchCardDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseSearchCardDialogRef: MatDialogRef<CaseSearchCardDialogComponent>,
    private toastr: ToastrService
  ) { }

  public searchKeyword: string = "";
  public sarchCardSubscription: any;

  public btnSarchCard(): void {
    if (this.searchKeyword == "" || this.searchKeyword == null) {
      this.toastr.error("Search input cannot be empty.", "Invalid Search");
    } else {
      let btnSarchCard: Element = document.getElementById("btnSarchCard");
      let btnCloseSarchCard: Element = document.getElementById("btnCloseSarchCard");

      btnSarchCard.setAttribute("disabled", "disabled");
      btnCloseSarchCard.setAttribute("disabled", "disabled");

      this.ayudaService.searchCard(this.searchKeyword);
      this.sarchCardSubscription = this.ayudaService.searchCardListObservable.subscribe(
        data => {
          this.toastr.success("Search successfully done.", "Success");

          this.caseSearchCardDialogRef.close([data, this.searchKeyword]);

          btnSarchCard.removeAttribute("disabled");
          btnCloseSarchCard.removeAttribute("disabled");

          if (this.sarchCardSubscription != null) this.sarchCardSubscription.unsubscribe();
        }
      );
    }
  }

  public btnCloseSearchDialog(): void {
    this.caseSearchCardDialogRef.close();

    if (this.sarchCardSubscription != null) this.sarchCardSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.sarchCardSubscription != null) this.sarchCardSubscription.unsubscribe();
  }

}
