import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CitizenService } from '../../citizen.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-citizen-clear-photo-dialog',
  templateUrl: './citizen-clear-photo-dialog.component.html',
  styleUrls: ['./citizen-clear-photo-dialog.component.css']
})
export class CitizenClearPhotoDialogComponent implements OnInit {

  constructor(
    private citizenService: CitizenService,
    public citizenClearPhotoDialogRef: MatDialogRef<CitizenClearPhotoDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public clearPhotoData: any,
    private toastr: ToastrService
  ) { }

  public clearPhotoSubscription: any;

  public btnClearPhoto() {
    let btnClearPhoto: Element = document.getElementById("btnClearPhoto");
    let btnCloseClearPhotoDialog: Element = document.getElementById("btnCloseClearPhotoDialog");

    btnClearPhoto.setAttribute("disabled", "disabled");
    btnCloseClearPhotoDialog.setAttribute("disabled", "disabled");

    this.citizenService.clearCitizensPhoto(this.clearPhotoData.objCitizensModel.Id);
    this.clearPhotoSubscription = this.citizenService.clearCitizenPhotoObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Photo was successfully cleared.", "Success");
          this.citizenClearPhotoDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnClearPhoto.removeAttribute("disabled");
          btnCloseClearPhotoDialog.removeAttribute("disabled");
        }

        if (this.clearPhotoSubscription != null) this.clearPhotoSubscription.unsubscribe();
      }
    );
  }

  public btnCloseClearPhotoDialog(): void {
    this.citizenClearPhotoDialogRef.close();

    if (this.clearPhotoSubscription != null) this.clearPhotoSubscription.unsubscribe();
  }

  ngOnInit() {

  }

  ngOnDestroy() {
    if (this.clearPhotoSubscription != null) this.clearPhotoSubscription.unsubscribe();
  }
}
