import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServiceGroupTemplateModel } from '../../services.model'
import { ServicesService } from '../../services.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';

@Component({
  selector: 'app-service-group-template-detail-dialog',
  templateUrl: './service-group-template-detail-dialog.component.html',
  styleUrls: ['./service-group-template-detail-dialog.component.css']
})
export class ServiceGroupTemplateDetailDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public serviceGroupTemplateDetailDialogRef: MatDialogRef<ServiceGroupTemplateDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceGroupTemplateData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Service Group Template";

  public serviceGroupTemplateModel: ServiceGroupTemplateModel = {
    Id: this.serviceGroupTemplateData.objServiceGroupTemplateModel.Id,
    ServiceGroupId: this.serviceGroupTemplateData.objServiceGroupTemplateModel.ServiceGroupId,
    TemplateName: this.serviceGroupTemplateData.objServiceGroupTemplateModel.TemplateName,
    Problem: this.serviceGroupTemplateData.objServiceGroupTemplateModel.Problem,
    Background: this.serviceGroupTemplateData.objServiceGroupTemplateModel.Background,
    Recommendation: this.serviceGroupTemplateData.objServiceGroupTemplateModel.Recommendation
  }

  public saveServiceGroupTemplateSubscription: any;

  public btnSaveServiceGroupTemplate(): void {
    let btnSaveServiceGroupTemplate: Element = document.getElementById("btnSaveServiceGroupTemplate");
    let btnCloseServiceGroupTemplateDialog: Element = document.getElementById("btnCloseServiceGroupTemplateDialog");

    btnSaveServiceGroupTemplate.setAttribute("disabled", "disabled");
    btnCloseServiceGroupTemplateDialog.setAttribute("disabled", "disabled");

    this.servicesService.saveServiceGroupTemplate(this.serviceGroupTemplateModel);
    this.saveServiceGroupTemplateSubscription = this.servicesService.saveServiceGroupTemplateObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Template was successfully saved.", "Success");
          this.serviceGroupTemplateDetailDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveServiceGroupTemplate.removeAttribute("disabled");
          btnCloseServiceGroupTemplateDialog.removeAttribute("disabled");
        }

        if (this.saveServiceGroupTemplateSubscription != null) this.saveServiceGroupTemplateSubscription.unsubscribe();
      }
    );
  }

  public btnCloseServiceGroupTemplateDialog(): void {
    this.serviceGroupTemplateDetailDialogRef.close();

    if (this.saveServiceGroupTemplateSubscription != null) this.saveServiceGroupTemplateSubscription.unsubscribe();
  }

  ngOnInit() {
    this.dialogTitle = this.serviceGroupTemplateData.objDialogTitle;
  }

  ngOnDestroy() {
    if (this.saveServiceGroupTemplateSubscription != null) this.saveServiceGroupTemplateSubscription.unsubscribe();
  }

}
