import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AyudaService } from '../../ayuda.service';
import { ToastrService } from 'ngx-toastr';
import { ObservableArray, CollectionView } from 'wijmo/wijmo';

@Component({
  selector: 'app-case-service-group-template-dialog',
  templateUrl: './case-service-group-template-dialog.component.html',
  styleUrls: ['./case-service-group-template-dialog.component.css']
})
export class CaseServiceGroupTemplateDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseServiceGroupTemplateDetailDialogRef: MatDialogRef<CaseServiceGroupTemplateDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public caseServiceGroupTemplateData: any,
    private toastr: ToastrService,
  ) { }

  public dialogTitle: string = "Service Group Template";

  public listServiceGroupTemplateSubscription: any;
  public listServiceGroupTemplateObservableArray: ObservableArray = new ObservableArray();
  public listServiceGroupTemplateCollectionView: CollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
  public listServiceGroupTemplatePageIndex: number = 10;

  public listServiceGroupTemplateData(serviceGroupId: number): void {
    this.listServiceGroupTemplateObservableArray = new ObservableArray();
    this.listServiceGroupTemplateCollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
    this.listServiceGroupTemplateCollectionView.pageSize = 15;
    this.listServiceGroupTemplateCollectionView.trackChanges = true;
    this.listServiceGroupTemplateCollectionView.refresh();

    this.ayudaService.listServiceGroupTemplate(serviceGroupId);
    this.listServiceGroupTemplateSubscription = this.ayudaService.listServiceGroupTemplateObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listServiceGroupTemplateObservableArray = data;
          this.listServiceGroupTemplateCollectionView = new CollectionView(this.listServiceGroupTemplateObservableArray);
          this.listServiceGroupTemplateCollectionView.pageSize = this.listServiceGroupTemplatePageIndex;
          this.listServiceGroupTemplateCollectionView.trackChanges = true;
          this.listServiceGroupTemplateCollectionView.refresh();
        }

        if (this.listServiceGroupTemplateSubscription != null) this.listServiceGroupTemplateSubscription.unsubscribe();
      }
    );
  }

  public btnCloseServiceGroupTemplateDialog(): void {
    this.caseServiceGroupTemplateDetailDialogRef.close(200);
  }

  public btnPickTemplate(): void {
    let currentTemplate = this.listServiceGroupTemplateCollectionView.currentItem;
    this.caseServiceGroupTemplateDetailDialogRef.close(currentTemplate);
  }

  ngOnInit() {
    this.listServiceGroupTemplateData(this.caseServiceGroupTemplateData.objServiceGroupId);
  }

  ngOnDestroy() {
    if (this.listServiceGroupTemplateSubscription != null) this.listServiceGroupTemplateSubscription.unsubscribe();
  }

}
