import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { CaseModel } from '../ayuda.model';

import { AyudaService } from '../ayuda.service';
import { ToastrService } from 'ngx-toastr';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { CaseServiceGroupTemplateDialogComponent } from './case-service-group-template-dialog/case-service-group-template-dialog.component';
import { CasePrintCaseDialogComponent } from './case-print-case-dialog/case-print-case-dialog.component';

@Component({
  selector: 'app-case-detail-dialog',
  templateUrl: './case-detail-dialog.component.html',
  styleUrls: ['./case-detail-dialog.component.css']
})
export class CaseDetailDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseDetailDialogRef: MatDialogRef<CaseDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public caseData: any,
    private toastr: ToastrService,
    public caseGetTemplateDialog: MatDialog,
    public casePrintCaseDialog: MatDialog,
  ) { }

  public dialogTitle: string = "Case Detail";

  public caseModel: CaseModel = {
    Id: this.caseData.objCaseModel.Id,
    CaseNumber: this.caseData.objCaseModel.CaseNumber,
    CaseDate: this.caseData.objCaseModel.CaseDate,
    CitizenId: this.caseData.objCaseModel.CitizenId,
    Citizen: this.caseData.objCaseModel.Citizen,
    CitizenDateOfBirth: this.caseData.objCaseModel.CitizenDateOfBirth,
    CitizenAge: this.caseData.objCaseModel.CitizenAge,
    CitizenCivilStatus: this.caseData.objCaseModel.CitizenCivilStatus,
    CitizenEducationalAttainment: this.caseData.objCaseModel.CitizenEducationalAttainment,
    CitizenOccupation: this.caseData.objCaseModel.CitizenOccupation,
    CitizenReligion: this.caseData.objCaseModel.CitizenReligion,
    CitizenAddress: this.caseData.objCaseModel.CitizenAddress,
    CitizenCardId: this.caseData.objCaseModel.CitizenCardId,
    CitizenCardNumber: this.caseData.objCaseModel.CitizenCardNumber,
    ServiceId: this.caseData.objCaseModel.ServiceId,
    Problem: this.caseData.objCaseModel.Problem,
    Background: this.caseData.objCaseModel.Background,
    Recommendation: this.caseData.objCaseModel.Recommendation,
    PreparedById: this.caseData.objCaseModel.PreparedById,
    CheckedById: this.caseData.objCaseModel.CheckedById,
    StatusId: this.caseData.objCaseModel.StatusId,
    IsLocked: this.caseData.objCaseModel.IsLocked
  }

  public listServiceSubscription: any;
  public listServiceObservableArray: ObservableArray = new ObservableArray();
  public listUserSubscription: any;
  public listUserObservableArray: ObservableArray = new ObservableArray();
  public listStatusSubscription: any;
  public listStatusObservableArray: ObservableArray = new ObservableArray();

  public saveCaseSubscription: any;
  public lockCaseSubscription: any;
  public unlockCaseSubscription: any;
  public printCaseSubscription: any;

  public listService(): void {
    this.ayudaService.listService();
    this.listServiceSubscription = this.ayudaService.listServiceObservable.subscribe(
      data => {
        this.listServiceObservableArray = data;
        if (this.listServiceSubscription != null) this.listServiceSubscription.unsubscribe();

        this.listUser();
      }
    );
  }

  public listUser(): void {
    this.ayudaService.listUser();
    this.listUserSubscription = this.ayudaService.listUserObservable.subscribe(
      data => {
        this.listUserObservableArray = data;
        if (this.listUserSubscription != null) this.listUserSubscription.unsubscribe();

        this.listStatus();
      }
    );
  }

  public listStatus(): void {
    this.ayudaService.listStatus();
    this.listStatusSubscription = this.ayudaService.listStatusObservable.subscribe(
      data => {
        this.listStatusObservableArray = data;
        if (this.listStatusSubscription != null) this.listStatusSubscription.unsubscribe();
      }
    );
  }

  public btnSaveCase(): void {
    let btnSaveCase: Element = document.getElementById("btnSaveCase");
    let btnLockCase: Element = document.getElementById("btnLockCase");
    let btnCloseCase: Element = document.getElementById("btnCloseCase");

    btnSaveCase.setAttribute("disabled", "disabled");
    btnLockCase.setAttribute("disabled", "disabled");
    btnCloseCase.setAttribute("disabled", "disabled");

    this.ayudaService.saveCase(this.caseModel);
    this.saveCaseSubscription = this.ayudaService.saveCaseObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Case was successfully saved.", "Success");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
        }

        btnSaveCase.removeAttribute("disabled");
        btnLockCase.removeAttribute("disabled");
        btnCloseCase.removeAttribute("disabled");

        if (this.saveCaseSubscription != null) this.saveCaseSubscription.unsubscribe();
      }
    );
  }

  public btnLockCase(): void {
    let btnSaveCase: Element = document.getElementById("btnSaveCase");
    let btnLockCase: Element = document.getElementById("btnLockCase");
    let btnCloseCase: Element = document.getElementById("btnCloseCase");

    btnSaveCase.setAttribute("disabled", "disabled");
    btnLockCase.setAttribute("disabled", "disabled");
    btnCloseCase.setAttribute("disabled", "disabled");

    this.ayudaService.lockCase(this.caseModel);
    this.lockCaseSubscription = this.ayudaService.lockCaseObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Case was successfully locked.", "Success");
          this.caseModel.IsLocked = true;
          btnCloseCase.removeAttribute("disabled");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnSaveCase.removeAttribute("disabled");
          btnLockCase.removeAttribute("disabled");
          btnCloseCase.removeAttribute("disabled");
        }

        if (this.lockCaseSubscription != null) this.lockCaseSubscription.unsubscribe();
      }
    );
  }

  public btnUnlockCase(): void {
    let btnSaveCase: Element = document.getElementById("btnSaveCase");
    let btnLockCase: Element = document.getElementById("btnLockCase");
    let btnUnlockCase: Element = document.getElementById("btnUnlockCase");
    let btnPrintCase: Element = document.getElementById("btnPrintCase");
    let btnCloseCase: Element = document.getElementById("btnCloseCase");

    btnSaveCase.setAttribute("disabled", "disabled");
    btnLockCase.setAttribute("disabled", "disabled");
    btnUnlockCase.setAttribute("disabled", "disabled");
    btnPrintCase.setAttribute("disabled", "btnPrintCase");
    btnCloseCase.setAttribute("disabled", "disabled");

    this.ayudaService.unlockCase(this.caseModel);
    this.unlockCaseSubscription = this.ayudaService.unlockCaseObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Case was successfully unlocked.", "Success");
          this.caseModel.IsLocked = false;
          btnCloseCase.removeAttribute("disabled");
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnUnlockCase.removeAttribute("disabled");
          btnPrintCase.removeAttribute("disabled");
          btnCloseCase.removeAttribute("disabled");
        }

        if (this.unlockCaseSubscription != null) this.unlockCaseSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDetaileDialog(): void {
    this.caseDetailDialogRef.close(200);

    if (this.listServiceSubscription != null) this.listServiceSubscription.unsubscribe();
    if (this.listUserSubscription != null) this.listUserSubscription.unsubscribe();
    if (this.listStatusSubscription != null) this.listStatusSubscription.unsubscribe();
    if (this.saveCaseSubscription != null) this.saveCaseSubscription.unsubscribe();
    if (this.lockCaseSubscription != null) this.lockCaseSubscription.unsubscribe();
    if (this.unlockCaseSubscription != null) this.unlockCaseSubscription.unsubscribe();
  }

  public btnGetTemplate(): void {
    let services = this.listServiceObservableArray.filter(service => service.Id === this.caseModel.ServiceId);
    let serviceGroupId = services[0]["ServiceGroupId"];

    const caseGetTemplateDialogRef = this.caseGetTemplateDialog.open(CaseServiceGroupTemplateDialogComponent, {
      width: '1100px',
      data: { objServiceGroupId: serviceGroupId },
      disableClose: true
    });

    caseGetTemplateDialogRef.afterClosed().subscribe(result => {
      if (result != 200) {
        this.caseModel.Problem = result.Problem;
        this.caseModel.Background = result.Background;
        this.caseModel.Recommendation = result.Recommendation;

        this.toastr.success("Template was successfully loaded.", "Success");
      }
    });
  }

  public btnPrintCase(documentType: string): void {
    this.casePrintCaseDialog.open(CasePrintCaseDialogComponent, {
      width: '1000px',
      data: {
        objId: this.caseModel.Id,
        objDocumentType: documentType
      },
      disableClose: true
    });
  }

  public btnPrintCaseSummaryReport(): void {
    this.btnPrintCase("Case Summary Report");
  }

  public btnPrintCertificateOfEligibility(): void {
    this.btnPrintCase("Certificate of Eligibility");
  }

  public btnPrintDisbursementVoucher(): void {
    this.btnPrintCase("Disbursement Voucher");
  }

  public btnPrintObligationRequest(): void {
    this.btnPrintCase("Obligation Request");
  }

  public btnPrintJournalEntryVoucher(): void {
    this.btnPrintCase("Journal Entry Voucher");
  }

  ngOnInit() {
    this.dialogTitle = this.caseData.objDialogTitle;
    this.listService();
  }

  ngOnDestroy() {
    if (this.listServiceSubscription != null) this.listServiceSubscription.unsubscribe();
    if (this.listUserSubscription != null) this.listUserSubscription.unsubscribe();
    if (this.listStatusSubscription != null) this.listStatusSubscription.unsubscribe();
    if (this.saveCaseSubscription != null) this.saveCaseSubscription.unsubscribe();
    if (this.lockCaseSubscription != null) this.lockCaseSubscription.unsubscribe();
    if (this.unlockCaseSubscription != null) this.unlockCaseSubscription.unsubscribe();
  }
}
