import { Component, OnInit, ViewChild, TemplateRef, EventEmitter } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl } from '@angular/forms';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { MatSelectChange } from '@angular/material/select';

import { AyudaService } from "./ayuda.service";
import { ObservableArray, CollectionView } from 'wijmo/wijmo';

import { ToastrService } from 'ngx-toastr';

import { CaseDetailDialogComponent } from './case-detail-dialog/case-detail-dialog.component';
import { CaseDeleteDialogComponent } from './case-delete-dialog/case-delete-dialog.component';
import { CaseCardDialogComponent } from './case-card-dialog/case-card-dialog.component';

import { CaseModel } from './ayuda.model';

@Component({
  selector: 'app-ayuda',
  templateUrl: './ayuda.component.html',
  styleUrls: ['./ayuda.component.css']
})
export class AyudaComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseDetailDialog: MatDialog,
    public caseDeleteDialog: MatDialog,
    public caseCardDialog: MatDialog,
    private toastr: ToastrService
  ) { }

  public caseModel: CaseModel = new CaseModel();

  public serviceDepartmentFilterSelectedValue: number = 0;
  public caseStartDateFilterFormControl = new FormControl(new Date());
  public caseEndDateFilterFormControl = new FormControl(new Date());

  public listServiceDepartmentSubscription: any;
  public listServiceDepartmentObservableArray: ObservableArray = new ObservableArray();

  public listCaseSubscription: any;
  public listCaseObservableArray: ObservableArray = new ObservableArray();
  public listCaseCollectionView: CollectionView = new CollectionView(this.listCaseObservableArray);
  public listCasePageIndex: number = 15;

  public listServiceDepartment(): void {
    this.ayudaService.listServiceDepartment();
    this.listServiceDepartmentSubscription = this.ayudaService.listServiceDepartmentObservable.subscribe(
      data => {
        this.listServiceDepartmentObservableArray = data;
        if (this.listServiceDepartmentSubscription != null) this.listServiceDepartmentSubscription.unsubscribe();
      }
    );
  }

  public caseDateFiltersDateChange(type: string, event: MatDatepickerInputEvent<Date>): void {
    this.listCaseData();
  }

  public serviceDepartmentFilterSelectionChange(event: EventEmitter<MatSelectChange>) {
    this.listCaseData();
  }

  public listCaseData(): void {
    if (this.serviceDepartmentFilterSelectedValue != 0) {
      let caseStartDateFilter: string = new Date(this.caseStartDateFilterFormControl.value).toLocaleDateString("fr-CA");
      let caseEndDateFilter: string = new Date(this.caseEndDateFilterFormControl.value).toLocaleDateString("fr-CA");
      let caseServiceDepartmentFilter: number = this.serviceDepartmentFilterSelectedValue;

      this.listCaseObservableArray = new ObservableArray();
      this.listCaseCollectionView = new CollectionView(this.listCaseObservableArray);
      this.listCaseCollectionView.pageSize = 15;
      this.listCaseCollectionView.trackChanges = true;
      this.listCaseCollectionView.refresh();

      this.ayudaService.listCase(caseServiceDepartmentFilter, caseStartDateFilter, caseEndDateFilter);
      this.listCaseSubscription = this.ayudaService.listCaseObservable.subscribe(
        data => {
          if (data.length > 0) {
            this.listCaseObservableArray = data;
            this.listCaseCollectionView = new CollectionView(this.listCaseObservableArray);
            this.listCaseCollectionView.pageSize = this.listCasePageIndex;
            this.listCaseCollectionView.trackChanges = true;
            this.listCaseCollectionView.refresh();
          }

          if (this.listCaseSubscription != null) this.listCaseSubscription.unsubscribe();
        }
      );
    }
  }

  public btnAddCase(): void {
    const caseCardDialogRef = this.caseCardDialog.open(CaseCardDialogComponent, {
      width: '1200px',
      disableClose: true
    });

    caseCardDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCaseData();
      }
    });
  }

  public btnEditCase(): void {
    let currentCase = this.listCaseCollectionView.currentItem;
    this.caseModel.Id = currentCase.Id;
    this.caseModel.CaseNumber = currentCase.CaseNumber;
    this.caseModel.CaseDate = new Date(currentCase.CaseDate);
    this.caseModel.CitizenId = currentCase.CitizenId;
    this.caseModel.Citizen = currentCase.Citizen;
    this.caseModel.CitizenDateOfBirth = currentCase.CitizenDateOfBirth;
    this.caseModel.CitizenAge = currentCase.CitizenAge;
    this.caseModel.CitizenCivilStatus = currentCase.CitizenCivilStatus;
    this.caseModel.CitizenEducationalAttainment = currentCase.CitizenEducationalAttainment;
    this.caseModel.CitizenOccupation = currentCase.CitizenOccupation;
    this.caseModel.CitizenReligion = currentCase.CitizenReligion;
    this.caseModel.CitizenAddress = currentCase.CitizenAddress;
    this.caseModel.CitizenCardId = currentCase.CitizenCardId;
    this.caseModel.CitizenCardNumber = currentCase.CitizenCardNumber;
    this.caseModel.ServiceId = currentCase.ServiceId;
    this.caseModel.Problem = currentCase.Problem;
    this.caseModel.Background = currentCase.Background;
    this.caseModel.Recommendation = currentCase.Recommendation;
    this.caseModel.PreparedById = currentCase.PreparedById;
    this.caseModel.CheckedById = currentCase.CheckedById;
    this.caseModel.StatusId = currentCase.StatusId;
    this.caseModel.IsLocked = currentCase.IsLocked;

    const caseDetailDialogRef = this.caseDetailDialog.open(CaseDetailDialogComponent, {
      width: '1200px',
      data: {
        objDialogTitle: "Edit Case",
        objCaseModel: this.caseModel
      },
      disableClose: true
    });

    caseDetailDialogRef.afterClosed().subscribe(result => {
      if (result == 200) {
        this.listCaseData();
      }
    });
  }

  public btnDeleteCase(): void {
    let currentCase = this.listCaseCollectionView.currentItem;

    if (currentCase.IsLocked == true) {
      this.toastr.error("Case cannot be deleted if locked.", "Error");
    } else {
      this.caseModel.Id = currentCase.Id;

      const caseDeleteDialogRef = this.caseDeleteDialog.open(CaseDeleteDialogComponent, {
        width: '300px',
        data: { objCaseModel: this.caseModel },
        disableClose: true
      });

      caseDeleteDialogRef.afterClosed().subscribe(result => {
        if (result == 200) {
          this.listCaseData();
        }
      });
    }
  }

  ngOnInit() {
    this.listServiceDepartment();
  }

  ngOnDestroy() {
    if (this.listServiceDepartmentSubscription != null) this.listServiceDepartmentSubscription.unsubscribe();
    if (this.listCaseSubscription != null) this.listCaseSubscription.unsubscribe();
  }
}
