import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServicesService } from '../../services.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-service-group-template-delete-dialog',
  templateUrl: './service-group-template-delete-dialog.component.html',
  styleUrls: ['./service-group-template-delete-dialog.component.css']
})
export class ServiceGroupTemplateDeleteDialogComponent implements OnInit {

  constructor(
    private servicesService: ServicesService,
    public ServiceGroupTemplateDeleteDialogRef: MatDialogRef<ServiceGroupTemplateDeleteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public childData: any,
    private toastr: ToastrService
  ) { }

  public deleteServiceGroupTemplateSubscription: any;

  public btnDeleteServiceGroupTemplate() {
    let btnDeleteServiceGroupTemplate: Element = document.getElementById("btnDeleteServiceGroupTemplate");
    let btnCloseDeleteDialog: Element = document.getElementById("btnCloseDeleteDialog");

    btnDeleteServiceGroupTemplate.setAttribute("disabled", "disabled");
    btnCloseDeleteDialog.setAttribute("disabled", "disabled");

    this.servicesService.deleteServiceGroupTemplate(this.childData.objServiceGroupTemplateModel.Id);
    this.deleteServiceGroupTemplateSubscription = this.servicesService.deleteServiceGroupTemplateObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.success("Template was successfully deleted.", "Success");
          this.ServiceGroupTemplateDeleteDialogRef.close(200);
        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnDeleteServiceGroupTemplate.removeAttribute("disabled");
          btnCloseDeleteDialog.removeAttribute("disabled");
        }

        if (this.deleteServiceGroupTemplateSubscription != null) this.deleteServiceGroupTemplateSubscription.unsubscribe();
      }
    );
  }

  public btnCloseDeleteDialog(): void {
    this.ServiceGroupTemplateDeleteDialogRef.close();

    if (this.deleteServiceGroupTemplateSubscription != null) this.deleteServiceGroupTemplateSubscription.unsubscribe();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (this.deleteServiceGroupTemplateSubscription != null) this.deleteServiceGroupTemplateSubscription.unsubscribe();
  }
}
