import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { ObservableArray, CollectionView } from 'wijmo/wijmo';
import { WjFlexGrid } from 'wijmo/wijmo.angular2.grid';

import { AyudaService } from '../ayuda.service';
import { CaseModel } from '../ayuda.model';

import { ToastrService } from 'ngx-toastr';

import { CaseSearchCardDialogComponent } from './case-search-card-dialog/case-search-card-dialog.component';
import { CaseDetailDialogComponent } from '../case-detail-dialog/case-detail-dialog.component';

@Component({
  selector: 'app-case-card-dialog',
  templateUrl: './case-card-dialog.component.html',
  styleUrls: ['./case-card-dialog.component.css']
})
export class CaseCardDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public caseCardDialogRef: MatDialogRef<CaseCardDialogComponent>,
    public caseSarchCardDialog: MatDialog,
    public caseDetailDialog: MatDialog,
    private toastr: ToastrService
  ) { }

  public listCardSubscription: any;
  public listCardObservableArray: ObservableArray = new ObservableArray();
  public listCardCollectionView: CollectionView = new CollectionView(this.listCardObservableArray);
  public listCardPageIndex: number = 7;

  public isFilterShow: boolean = false;
  public filterString: string = "";

  public caseModel: CaseModel = new CaseModel();

  public pickCaseSubscription: any;
  public detailCaseSubscription: any;

  public listCardData(): void {
    this.listCardObservableArray = new ObservableArray();
    this.listCardCollectionView = new CollectionView(this.listCardObservableArray);
    this.listCardCollectionView.pageSize = 15;
    this.listCardCollectionView.trackChanges = true;
    this.listCardCollectionView.refresh();

    this.ayudaService.listCard();
    this.listCardSubscription = this.ayudaService.listCardObservable.subscribe(
      data => {
        if (data.length > 0) {
          this.listCardObservableArray = data;
          this.listCardCollectionView = new CollectionView(this.listCardObservableArray);
          this.listCardCollectionView.pageSize = this.listCardPageIndex;
          this.listCardCollectionView.trackChanges = true;
          this.listCardCollectionView.refresh();
        }

        if (this.listCardSubscription != null) this.listCardSubscription.unsubscribe();
      }
    );
  }

  public btnSearchCard(): void {
    const sarchCardlDialogRef = this.caseSarchCardDialog.open(CaseSearchCardDialogComponent, {
      width: '800px',
      disableClose: true
    });

    sarchCardlDialogRef.afterClosed().subscribe(result => {
      if (result != null) {
        if (result[1] != "" || result[1] != null) {
          this.isFilterShow = true;
          this.filterString = result[1];
        }

        this.listCardObservableArray = result[0];
        this.listCardCollectionView = new CollectionView(this.listCardObservableArray);
        this.listCardCollectionView.pageSize = this.listCardPageIndex;
        this.listCardCollectionView.trackChanges = true;
        this.listCardCollectionView.refresh();
      }
    });
  }

  public btnClearFilter(): void {
    this.isFilterShow = false;
    this.filterString = "";

    this.listCardData();
  }

  public btnPickCard(): void {
    let btnPickCard: Element = document.getElementById("btnPickCard");
    btnPickCard.setAttribute("disabled", "disabled");

    let currentCard = this.listCardCollectionView.currentItem;
    this.ayudaService.addCase(currentCard.Id);
    this.pickCaseSubscription = this.ayudaService.addCaseObservable.subscribe(
      data => {
        if (data[0] == "success") {
          this.toastr.info("Case was successfully created as draft.", "Info");
          this.getCaseDetail(parseInt(data[1]));

        } else if (data[0] == "failed") {
          this.toastr.error(data[1], "Error");
          btnPickCard.removeAttribute("disabled");
        }

        if (this.pickCaseSubscription != null) this.pickCaseSubscription.unsubscribe();
      }
    );
  }

  public getCaseDetail(id: number): void {
    let btnPickCard: Element = document.getElementById("btnPickCard");

    this.ayudaService.detailCase(id);
    this.detailCaseSubscription = this.ayudaService.detailCaseObservable.subscribe(
      data => {
        if (data != null) {
          this.caseModel.Id = data.Id;
          this.caseModel.CaseNumber = data.CaseNumber;
          this.caseModel.CaseDate = new Date(data.CaseDate);
          this.caseModel.CitizenId = data.CitizenId;
          this.caseModel.Citizen = data.Citizen;
          this.caseModel.CitizenDateOfBirth = data.CitizenDateOfBirth;
          this.caseModel.CitizenAge = data.CitizenAge;
          this.caseModel.CitizenCivilStatus = data.CitizenCivilStatus;
          this.caseModel.CitizenEducationalAttainment = data.CitizenEducationalAttainment;
          this.caseModel.CitizenOccupation = data.CitizenOccupation;
          this.caseModel.CitizenReligion = data.CitizenReligion;
          this.caseModel.CitizenAddress = data.CitizenAddress;
          this.caseModel.CitizenCardId = data.CitizenCardId;
          this.caseModel.CitizenCardNumber = data.CitizenCardNumber;
          this.caseModel.ServiceId = data.ServiceId;
          this.caseModel.Problem = data.Problem;
          this.caseModel.Recommendation = data.Recommendation;
          this.caseModel.PreparedById = data.PreparedById;
          this.caseModel.CheckedById = data.CheckedById;
          this.caseModel.StatusId = data.StatusId;
          this.caseModel.IsLocked = data.IsLocked;

          const caseDetailDialogRef = this.caseDetailDialog.open(CaseDetailDialogComponent, {
            width: '1200px',
            data: {
              objDialogTitle: "Add Case",
              objCaseModel: this.caseModel
            },
            disableClose: true
          });

          caseDetailDialogRef.afterClosed().subscribe(result => {
            if (result == 200) {
              this.caseCardDialogRef.close(200);
            }
          });
        }

        if (this.detailCaseSubscription != null) this.detailCaseSubscription.unsubscribe();
        btnPickCard.removeAttribute("disabled");
      }
    );
  }

  public btnCloseCardDialog(): void {
    this.caseCardDialogRef.close(200);

    if (this.listCardSubscription != null) this.listCardSubscription.unsubscribe();
    if (this.detailCaseSubscription != null) this.detailCaseSubscription.unsubscribe();
    if (this.pickCaseSubscription != null) this.pickCaseSubscription.unsubscribe();
  }

  ngOnInit() {
    this.listCardData();

    setTimeout(() => {
      this.btnSearchCard();
    }, 100);
  }

  ngOnDestroy() {
    if (this.listCardSubscription != null) this.listCardSubscription.unsubscribe();
    if (this.detailCaseSubscription != null) this.detailCaseSubscription.unsubscribe();
    if (this.pickCaseSubscription != null) this.pickCaseSubscription.unsubscribe();
  }
}
