import { Component, OnInit, Inject, ViewContainerRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Router, ActivatedRoute } from '@angular/router';

import { AyudaService } from '../../ayuda.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-case-print-case-dialog',
  templateUrl: './case-print-case-dialog.component.html',
  styleUrls: ['./case-print-case-dialog.component.css']
})
export class CasePrintCaseDialogComponent implements OnInit {

  constructor(
    private ayudaService: AyudaService,
    public casePrintCaseDialogRef: MatDialogRef<CasePrintCaseDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public caseData: any,
    private toastr: ToastrService
  ) { }

  public printCaseSubscription: any;
  public pdfUrl: string;

  public documentName: string;

  public printCase(): void {
    let documentType: string = this.caseData.objDocumentType;
    this.documentName = documentType;

    this.ayudaService.printCase(this.caseData.objId, documentType);
    this.printCaseSubscription = this.ayudaService.printCaseObservable.subscribe(
      data => {
        var binaryData = [];
        binaryData.push(data);
        this.pdfUrl = URL.createObjectURL(new Blob(binaryData, { type: "application/pdf" }));

        let printPDF: Element = document.getElementById("printPDF");
        printPDF.setAttribute("src", this.pdfUrl);

        if (this.printCaseSubscription != null) this.printCaseSubscription.unsubscribe();
      }
    );
  }

  public btnClosePrintDialog(): void {
    this.casePrintCaseDialogRef.close();
    if (this.printCaseSubscription != null) this.printCaseSubscription.unsubscribe();
  }

  public btnPrintCase(): void {
    window.frames["printPDF"].focus();
    window.frames["printPDF"].print();
  }

  ngOnInit() {
    this.printCase();
  }

  ngOnDestroy() {
    if (this.printCaseSubscription != null) this.printCaseSubscription.unsubscribe();
  }

}
